/* Custom job section refrenced from legal02 */
.ContentSection {
  width: 90%;
  margin: auto;
  margin-top: 25px;
}
html {
  scroll-behavior: smooth;
}
.Clientheading {
  font-family: "Merriweather", serif;
  font-weight: bolder;
  font-size: 1.6rem;
  position: relative;
  left: -43%;
  right: 650px;
  font-weight: 900;
  text-transform: uppercase;
  padding: 5% 5% 2% 5%;
  color: #fff;
}
.bg {
  background: black;
  padding: 15px;
}
.handleitems {
  margin-top: 50px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 0px;
  row-gap: 0px;
  max-width: 95%;
  margin: 0 auto;
}
.showcase_item {
  /* background-color: #fff; */
  border-color: #000;
  border-style: solid;
  border-width: 1px 1px 1px 1px;
  background-image: linear-gradient(to bottom, #f4ebd5, #c0760a);
  color: #000;
  overflow: hidden;
  position: relative;
  border-bottom: none;
}
.showcase_item--single {
  border-bottom: 1px solid #000;
}

.showcase_item_body {
  max-width: 95%;
}
.showcase_item__content {
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  text-align: left;
  font-family: "Merriweather", serif;
  max-width: 100%;
  top: 0;
  /* background-color: #fff; */
  padding: 20px;
  transition: opacity 0.3s ease-out;
}
