.display--none {
  display: none;
}
.title {
  text-transform: uppercase;
  font-size: 20px;
}
.showcase_item:before {
  display: block;
  content: "";
  width: 100%;
  padding-top: 75%;
}

.showcase_item__content .link {
  bottom: 0px;
  padding-left: 20px;
  position: absolute;
  text-align: right;
  right: 26px;
  background: transparent;
  width: 90%;
}
.p-4 {
  padding: 1.5rem !important;
}
h6 {
  font-size: 1rem;
  margin: 8px 0px;
  text-align: left;
}
p {
  line-height: 1.5rem;
}
.showcase_item__content .showcase_item_body {
  font-size: 15px;
  overflow-y: scroll;
  overflow-x: hidden;
  padding-right: 15px;
  height: 80%;
}

#show_items::-webkit-scrollbar-track {
  background-color: transparent;
}
.link:before {
  border: solid #000;
  border-width: 0 3px 3px 0;
  content: "";
  display: block;
  margin-top: -4px;
  padding: 3px;
  position: absolute;
  right: -18px;
  top: 50%;
  transform: rotate(-45deg);
  border-color: #f7323f;
}
