.navbar-space {
  position: fixed;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  z-index: 95;
  background: transparent;
  /* background: red; */
}
.navbar-container {
  /* height: 60px; */
  position: fixed;
  top: 0 !important;
  z-index: 100;
  background-color: rgba(0, 0, 0, 0.85);
  width: 100vw;
  height: 3.5rem;
  -webkit-transform-origin: top;
          transform-origin: top;
  -webkit-transform: scale(1, 0);
          transform: scale(1, 0);
  opacity: 0;
  transition: opacity 0.4s ease-out, -webkit-transform 0.1s ease-out;
  transition: transform 0.1s ease-out, opacity 0.4s ease-out;
  transition: transform 0.1s ease-out, opacity 0.4s ease-out, -webkit-transform 0.1s ease-out;
  /* border: 1px solid red; */
}
.navbar-container.show {
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
  opacity: 1;
  transition: opacity 0.1s ease-in;
}
.navbar {
  display: flex;
  justify-content: space-between;
  z-index: 100;
  width: 80%;
  margin: auto;
}
.navbar-container a:hover {
  color: rgb(255, 196, 81);
}
.hamburger-icon {
  display: none;
}
.navbar-logo {
  height: 40px;
  width: 50px;
}

.navbar-links-container {
  display: flex;
  align-items: center;
  /* border: 1px solid red; */
}
.navbar-links {
  color: white;
  list-style: none;
  display: flex;
  grid-gap: 25px;
  gap: 25px;
  font-size: 0.95rem;
  font-weight: 600;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  transition: all 0.3s;
}
.navbar-links a:hover {
  color: rgb(243, 193, 93);
}
.navbar-links-mobile {
  display: none;
}
.navbar-controls {
  display: flex;
  align-items: center;
  grid-gap: 15px;
  gap: 15px;
  /* border: 1px solid red; */
  /* margin-right: min(4vw, 4rem); */
}
.navbar-icon {
  /* stroke: yellow; */
  fill: rgb(243, 193, 93);
  /* outline: yellow; */
  display: inline-block;
  height: 28px;
  width: 28px;
  cursor: pointer;
}

@media only screen and (max-width: 900px) {
  .navbar-links {
    display: none;
  }

  .navbar-links-mobile {
    display: flex;
    flex-direction: column;
    grid-gap: 15px;
    gap: 15px;
    padding: 0px 120px 20px;
    position: absolute;
    top: -300px;
    width: 100%;
    left: 0;
    z-index: 10 !important;
    background-color: inherit;
    color: white;
    list-style: none;
    opacity: 0;
    transition: top 0.8s ease-in-out, opacity 0.2s ease;
  }

  .navbar-links-mobile.active {
    opacity: 1;
    top: 44px;
    transition: top 0.3s ease-in-out, opacity 0.3s ease-in-out 0.2s;
  }

  .navbar-links-mobile li {
    font-size: 0.95rem;
    font-weight: 600;
    padding: 0.25rem 1.5rem;
  }

  .hamburger-icon {
    display: inline-block;
    margin-right: 20vh;
    cursor: pointer;
  }
  .hamburger-icon svg {
    fill: rgb(250, 197, 128);
    width: 32px;
    height: 32px;
  }
}

@media only screen and (max-width: 600px) {
  .navbar {
    width: 95%;
  }
  .hamburger-icon {
    margin-right: 15px;
  }
  .navbar-links-mobile {
    padding-left: 30px;
  }
}

.header-section {
  height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-position: top;
  overflow: hidden;
}
.header-container {
  padding-top: 195px;
  display: block;
  text-align: center;
  margin: 0px auto;
}

.header-title-main {
  font-size: 50px;
  line-height: 64px;
  padding: 0px;
  margin: 0px;
  font-weight: 700;
  color: white;
}
.header-subtitle-main {
  color: hsla(0, 0%, 100%, 0.9);
  margin: 8px 0px 0px;
  font-size: 23.19px;
  font-weight: 400;
}

.header-options-main {
  display: grid;
  grid-template-columns: repeat(4, 160px);
  justify-content: center;
  margin-top: 70px;
  grid-gap: 25px;
  gap: 25px;
}

@media only screen and (max-width: 900px) {
  .header-container {
    padding-top: 150px;
  }
  .header-section {
    height: 100vh;
    background-position: 70% 0%;
  }

  .header-title-main {
    font-size: 40px;
    line-height: 42px;
  }
  .header-subtitle-main {
    font-size: 20px;
    line-height: 22px;
  }
  .header-options-main {
    grid-template-columns: repeat(2, 160px);
  }
}
@media only screen and (max-width: 600px) {
  .header-container {
    padding-top: 100px;
  }
}

.header-icon-box {
  border: 1px solid rgba(255, 255, 255, 0.342);
  height: 130px;
  width: 160px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* padding: 10px 0px 0px; */
  transition: all 0.3s ease-in-out;
}
.header-icon-box:hover {
  border-color: #ffc451;
}
.header-icon-box img {
  height: 50px;
  width: 50px;
}

.header-icon-box h3 {
  color: white;
  cursor: pointer;
  font-size: 20px;
  font-weight: 500;
  margin: 5pxpx 10px;
  line-height: 25px;
  transition: all 0.3s ease-in-out;
}
.header-icon-box h3:hover {
  color: #ffc451;
}

/* some common css derived in index.css */

#website-filters {
  /* border: 1px solid red; */
  padding: 0;
  margin: 0.5rem;
  /* margin: 20px auto 10px auto; */
  list-style: none;
  text-align: center;
  overflow: auto;
  white-space: nowrap;
}

/* Hide scrollbar for Chrome, Safari and Opera */
#website-filters::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
#website-filters {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

#website-filters li {
  cursor: pointer;
  display: inline-block;
  padding: 8px 15px 10px 15px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1;
  text-transform: uppercase;
  color: #444444;
  margin-bottom: 5px;
  transition: all 0.6s ease-in-out;
  border-radius: 3px;
}

#website-filters li:hover {
  color: rgba(255, 94, 19);
  background-color: transparent;
}

#website-filters li.filter-active {
  color: #151515;
  background: #ffc451;
}

#website-filters li:last-child {
  margin-right: 0;
}

.category {
