}
.carousel-drag {
  display: flex;
  justify-content: center;
}
.mobile_websites_container {
  padding: 0.5rem;
}

.flipcard {
  width: 33vw;
  max-width: 600px;
  min-width: 300px;
  aspect-ratio: 100 / 51;
  background-size: 100% 100%;
  overflow: hidden;
  background-color: transparent;
  border-radius: 8px;
  position: relative;
  margin: 10px;
  box-shadow: rgb(122, 104, 85) 8px 8px 20px;
}

.btn-demo {
  padding: 0;
  position: absolute;
  top: 25px;
  right: 10px;
}

.btn-demo button {
  height: 30px;
  width: 50px;
  border: none;
  border-radius: 5px;
  background-color: rgba(146, 135, 105, 0.4);
  font-size: 16px;
  padding: 0px;
  font-weight: 500;
  color: white;
  -webkit-animation: color-change 2s infinite;
          animation: color-change 2s infinite;
}

@-webkit-keyframes color-change {
  0% {
    color: white;
  }
  50% {
    color: black;
  }
  100% {
    color: white;
  }
}

@keyframes color-change {
  0% {
    color: white;
  }
  50% {
    color: black;
  }
  100% {
    color: white;
  }
}

.btn-demo button:hover {
  cursor: pointer;
  color: rgba(170, 170, 170);
}

.btn-flip {
  position: absolute;
  bottom: 20px;
  right: 5px;
  height: 24px;
  width: 24px;
  border-radius: 50%;
  padding: 0;
  background-color: #be8d03;
  border: none;
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
  overflow: hidden;
  z-index: 2;
}

.btn-flip div {
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn-flip div:hover {
  cursor: pointer;
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}

.flipcard-back {
  padding: 20px 6px 20px 14px;
}

.website-details {
  height: 100%;
  width: 98%;
  padding-right: 2%;
  overflow: auto;
  text-align: justify;
  font-size: 14px;
  font-weight: 200;
  scroll-behavior: smooth;
  font-family: "Rubik", sans-serif;
  color: black;
  background-color: transparent;
  line-height: 1.8;
}

.website-details::-webkit-scrollbar {
  width: 7px;
  height: 50px;
  background: linear-gradient(#feeab3, transparent);
  border-radius: 8px;
}

.website-details::-webkit-scrollbar-thumb {
  background: linear-gradient(#e3a702, transparent);
  border-radius: 8px;
  border: 1px solid #e3a702;
}
.contact-link {
  margin: 0;
  padding: 0;
  margin-right: 3px;
}

.contact-link:hover {
  cursor: pointer;
}

.contactpic {
  margin: 0;
  padding: 0;
  height: 18px;
  width: 18px;
}

@media only screen and (max-width: 1110px) {
  .flipcard {
    height: auto;
    width: auto;
    aspect-ratio: 100 / 51;
    min-width: 300px;
    min-height: 0;
    max-height: none;
    max-width: 500px;
    /* margin: 10px 0; */
    margin: 5px auto;
    box-shadow: none;
  }
  .react-card-flip:nth-child(2n) {
    margin-bottom: 20px;
  }
  .flipcard .img-fluid {
    height: 100%;
    width: auto;
    border-radius: 8px;
  }
}

.graphics-phone-option {
  display: none;
}
.graphics-categories {
  margin: 3vh 0;
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: 1.5rem;
  gap: 1.5rem;
  justify-content: center;
}
.graphics-phone-item {
  display: block;
}

@media only screen and (max-width: 600px) {
  .graphics-categories {
    display: block;
    min-height: 650px;
  }
  .graphics-phone-option {
    display: flex;
    flex-direction: row;
    overflow: hidden;
    color: #555;
  }
  .graphics-phone-option div {
    display: flex;
    grid-gap: 5px;
    gap: 5px;
    -webkit-animation: marquee 19s linear infinite;
            animation: marquee 19s linear infinite;
    margin: 20px 2px;
  }
  .graphics-phone-option span {
    white-space: nowrap;
    font-size: 16px;
    font-weight: 500;
  }
  .graphics-phone-option span.active {
    font-weight: bold;
    color: #ff5e13;
  }

  .graphics-phone-item {
    display: none;
  }
  .graphics-phone-item.active {
    display: block;
  }
}

@-webkit-keyframes marquee {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  100% {
    -webkit-transform: translate(-100%, 0);
            transform: translate(-100%, 0);
  }
}

@keyframes marquee {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  100% {
    -webkit-transform: translate(-100%, 0);
            transform: translate(-100%, 0);
  }
}

