}

.share {
  position: absolute;
  left: 4%;
  bottom: 0.8%;
}
.share a {
  all: unset;
  cursor: pointer;
}
.share i {
  font-size: clamp(1.05rem, 3vw, 1.2rem);
  color: var(--color3);
}

