.p_table {
  --color1: #c2780a;
  --color1-light: hsl(36, 90%, 35%);
  --color1-light-shade: hsl(36, 71%, 91%);
  --color2: #f4ebd5;
  --first-col-size: max(13vw, 15rem);
  /* --sticky-selected-left: max(10vw, 6rem); */
  --sticky-selected-left: calc(var(--first-col-size) * 1);
  --sticky-selected-right: 0;
  --thead-height: max(10vw, 7rem);
  /* margin: 5vh 10vw 0; */

  margin: 0vh 10vw;
  padding: 0;

  overflow: scroll;
  scroll-behavior: smooth;
  height: 85vh;

  /* border: 1px solid black; */
}
.cbg {
  color: red;
}
/* FOR SCROLL BARS */
.p_table::-webkit-scrollbar {
  height: 0.5rem !important;
  width: 0rem !important;
}
/* ARROWS  */
.left-arrow,
.right-arrow {
  border: none;
  background: none;
  padding: 0;
  margin: 0;
}

.left-arrow i,
.right-arrow i {
  font-size: clamp(2rem, 4vw, 4rem);
  color: var(--color1);
}
.left-arrow i:hover,
.right-arrow i:hover {
  color: hsl(36, 85%, 63%);
}

.left-arrow.disable,
.right-arrow.disable {
  opacity: 0.2;
}

/* .p_table::-webkit-scrollbar-thumb {
  border-radius: 0;
} */

.p_table table {
  padding: 0;
  margin: 0;
  border-collapse: separate;
  border-spacing: 0;
  table-layout: fixed;
  /* border: 1px solid black; */
}
.p_table thead {
  position: -webkit-sticky;
  position: sticky;
  z-index: 2;
}
.p_table tbody {
  position: -webkit-sticky;
  position: sticky;
  z-index: 1;
}
.p_table thead tr {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  /* top: 5%; */
  background: white;
}
.p_table td {
  background: white;
}
.p_table tbody td {
  padding: 1.2vh 0.5vw;
  /* border-bottom: 1px solid;
  border-left: 1px solid;
  border-right: 1px solid; */
  text-align: center;
  z-index: 1;
  font-size: clamp(0.6rem, 3vw, 1rem);
}

.p_table thead td {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background: var(--color1-light-shade);
  z-index: 4;
  cursor: default;
  padding: 1vh min(2.5vw, 2rem);
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  /* border: 1px solid black; */
  height: var(--thead-height);
}

.p_table thead td:first-child {
  position: -webkit-sticky;
  position: sticky;
  top: 6rem;
  left: 0%;

  background: white;
  z-index: 5 !important;
  width: var(--first-col-size);
  border-right: 1px solid rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
}
.p_table tbody td:first-child {
  position: -webkit-sticky;
  position: sticky;
  left: 0;
  z-index: 1;
  background: white;
  text-align: left;
  border: none;
  border-right: 1px solid rgba(0, 0, 0, 0.2);
  font-weight: 600;

  /* transform: translateY(15%); */
}
.p_table tbody td:first-child .services {
  position: absolute;
  inset: 0;
  z-index: 5 !important;
  margin: 0.4vw;
  cursor: default;
  display: flex;
  align-items: center;
}
/* tool tip */
.p_table tbody td:first-child .services.show::before {
  position: absolute;
  left: 0%;
  z-index: 5;
  -webkit-transform: scale(1, 0);
          transform: scale(1, 0);
  content: attr(data-i);
  width: -webkit-max-content;
  width: max-content;
  padding: 0.1rem 0.3rem;
  background: var(--color1-light);
  color: white;
}

.p_table tbody td:first-child .services.show:hover::before {
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
  -webkit-transform-origin: top;
          transform-origin: top;
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.p_table tbody td:first-child .services div {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: clamp(0.6rem, 3vw, 1.1rem);
  opacity: 1;
  transition: opacity 0.4s ease-out;
}
.p_table tbody td:first-child .services.show:hover div {
  opacity: 0;
  transition: opacity 0.2s ease-in;
}

.p_table thead td.choosed {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: var(--sticky-selected-left);
  right: var(--sticky-selected-right);
  z-index: 5;
  border: 3px solid var(--color1);
  border-bottom: none;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}

.p_table tbody td.choosed {
  position: -webkit-sticky;
  position: sticky;
  z-index: 0;
  left: var(--sticky-selected-left);
  right: var(--sticky-selected-right);

  border-left: 3px solid var(--color1);
  border-right: 3px solid var(--color1);
}
.p_table tbody tr:last-child td.choosed {
  position: -webkit-sticky;
  position: sticky;
  left: var(--sticky-selected-left);
  right: var(--sticky-selected-right);
  z-index: 2;
  border-bottom: 3px solid var(--color1);
  border-left: 3px solid var(--color1);
  border-right: 3px solid var(--color1);
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

