}

.footer-social-links {
  display: flex;
  align-items: center;
  grid-gap: 4px;
  gap: 4px;
}

.footer-contacts-options {
  display: flex;
  align-items: center;
  grid-gap: 4px;
  gap: 4px;
}

.footer-icons {
  fill: white;
}

@media only screen and (max-width: 600px) {
  #footer {
    font-size: 15px;
  }
  .footer-contact-links {
